//autofill input form
//Фон и цвет текста для input который был заполнен через автозаполнение:
@keyframes autofill {
  to {
    color: inherit;
    background: transparent;
  }
}


@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.show {
  animation: fadeIn .4s linear;
}

.hide {
  animation: fadeOut .4s linear;
}
